import React, { useState } from 'react';
import { useQuery, gql } from '@apollo/client';

const GET_BALANCE = gql`
  query GetBalance($walletId: Int!) {
    balance(walletId: $walletId) {
      ether
      wei
      address
    }
  }
`;

function WalletCard({ wallet, onSelect }) {
  const [showBalance, setShowBalance] = useState(false);
  
  const { data, loading, refetch } = useQuery(GET_BALANCE, {
    variables: { walletId: wallet.id },
    skip: !showBalance,
  });

  const handleCheckBalance = () => {
    if (showBalance) {
      refetch();
    } else {
      setShowBalance(true);
    }
  };

  const copyToClipboard = (text) => {
    navigator.clipboard.writeText(text);
    alert('Address copied to clipboard!');
  };

  return (
    <div className="wallet-card">
      <div className="wallet-card-header">
        <h3> Wallet #{wallet.id}</h3>
        <span className="wallet-badge">{wallet.network}</span>
      </div>

      <div className="wallet-card-body">
        <div className="wallet-info">
          <label>Address:</label>
          <div className="address-display">
            <span className="address-text">
              {wallet.address.substring(0, 10)}...{wallet.address.substring(wallet.address.length - 8)}
            </span>
            <button
              onClick={() => copyToClipboard(wallet.address)}
              className="btn-icon"
              title="Copy address"
            >
              
            </button>
          </div>
        </div>

        <div className="wallet-info">
          <label>Created:</label>
          <span>{new Date(wallet.createdAt).toLocaleDateString()}</span>
        </div>

        {showBalance && (
          <div className="balance-display">
            {loading ? (
              <div className="loading-small">Loading...</div>
            ) : data ? (
              <>
                <div className="balance-amount">{parseFloat(data.balance.ether).toFixed(4)} ETH</div>
                <div className="balance-wei">{data.balance.wei} wei</div>
              </>
            ) : null}
          </div>
        )}
      </div>

      <div className="wallet-card-footer">
        <button
          onClick={handleCheckBalance}
          className="btn btn-small btn-secondary"
        >
           {showBalance ? 'Refresh' : 'Check'} Balance
        </button>
        <button
          onClick={() => onSelect(wallet)}
          className="btn btn-small btn-primary"
        >
           Send
        </button>
        <a
          href={`https://sepolia.etherscan.io/address/${wallet.address}`}
          target="_blank"
          rel="noopener noreferrer"
          className="btn btn-small"
        >
           Etherscan
        </a>
      </div>
    </div>
  );
}

export default WalletCard;
