import React, { useState } from 'react';
import { useQuery, useMutation, gql } from '@apollo/client';
import { useNavigate } from 'react-router-dom';
import WalletCard from '../components/WalletCard';
import CreateWallet from '../components/CreateWallet';
import SendFunds from '../components/SendFunds';
import '../styles/Dashboard.css';

const GET_WALLETS = gql`
  query GetWallets {
    wallets {
      id
      address
      network
      createdAt
    }
  }
`;

function Dashboard() {
  const navigate = useNavigate();
  const username = localStorage.getItem('username');
  const [activeTab, setActiveTab] = useState('wallets');
  const [selectedWallet, setSelectedWallet] = useState(null);

  const { data, loading, error, refetch } = useQuery(GET_WALLETS);

  const handleLogout = () => {
    localStorage.removeItem('token');
    localStorage.removeItem('username');
    navigate('/login');
  };

  if (loading) return (
    <div className="loading-container">
      <div className="loading-spinner"></div>
      <p>Loading wallets...</p>
    </div>
  );

  if (error) return (
    <div className="error-container">
      <p>Error loading wallets: {error.message}</p>
      <button onClick={() => refetch()} className="btn">Retry</button>
    </div>
  );

  const wallets = data?.wallets || [];

  return (
    <div className="dashboard">
      {/* Header */}
      <header className="dashboard-header">
        <div className="header-content">
          <div className="user-info">
            <div className="user-avatar">{username?.charAt(0).toUpperCase()}</div>
            <div>
              <h2>Welcome, {username}!</h2>
              <p>Sepolia Testnet</p>
            </div>
          </div>
          <button onClick={handleLogout} className="btn btn-secondary">
            Logout
          </button>
        </div>
      </header>

      {/* Stats */}
      <div className="stats-container">
        <div className="stat-card">
          <div className="stat-icon"></div>
          <div className="stat-value">{wallets.length}</div>
          <div className="stat-label">Total Wallets</div>
        </div>
        <div className="stat-card">
          <div className="stat-icon"></div>
          <div className="stat-value">Sepolia</div>
          <div className="stat-label">Network</div>
        </div>
        <div className="stat-card">
          <div className="stat-icon"></div>
          <div className="stat-value">Secure</div>
          <div className="stat-label">Encrypted</div>
        </div>
      </div>

      {/* Tabs */}
      <div className="tabs">
        <button
          className={activeTab === 'wallets' ? 'tab active' : 'tab'}
          onClick={() => setActiveTab('wallets')}
        >
           My Wallets
        </button>
        <button
          className={activeTab === 'create' ? 'tab active' : 'tab'}
          onClick={() => setActiveTab('create')}
        >
           Create Wallet
        </button>
        <button
          className={activeTab === 'send' ? 'tab active' : 'tab'}
          onClick={() => setActiveTab('send')}
        >
           Send Funds
        </button>
      </div>

      {/* Content */}
      <div className="dashboard-content">
        {activeTab === 'wallets' && (
          <div className="wallets-section">
            <div className="section-header">
              <h3>My Wallets</h3>
              <button onClick={() => refetch()} className="btn btn-small">
                 Refresh
              </button>
            </div>

            {wallets.length === 0 ? (
              <div className="empty-state">
                <div className="empty-icon"></div>
                <h3>No Wallets Yet</h3>
                <p>Create your first wallet to get started!</p>
                <button
                  onClick={() => setActiveTab('create')}
                  className="btn btn-primary"
                >
                  Create Wallet
                </button>
              </div>
            ) : (
              <div className="wallet-grid">
                {wallets.map((wallet) => (
                  <WalletCard
                    key={wallet.id}
                    wallet={wallet}
                    onSelect={(w) => {
                      setSelectedWallet(w);
                      setActiveTab('send');
                    }}
                  />
                ))}
              </div>
            )}
          </div>
        )}

        {activeTab === 'create' && (
          <CreateWallet onSuccess={() => {
            refetch();
            setActiveTab('wallets');
          }} />
        )}

        {activeTab === 'send' && (
          <SendFunds
            wallets={wallets}
            selectedWallet={selectedWallet}
            onSuccess={() => refetch()}
          />
        )}
      </div>
    </div>
  );
}

export default Dashboard;
