import React, { useState } from 'react';
import { useMutation, gql } from '@apollo/client';

const CREATE_WALLET_MUTATION = gql`
  mutation CreateWallet($input: CreateWalletInput) {
    createWallet(input: $input) {
      id
      address
      network
      mnemonic
      createdAt
    }
  }
`;

function CreateWallet({ onSuccess }) {
  const [network, setNetwork] = useState('sepolia');
  const [result, setResult] = useState(null);

  const [createWallet, { loading, error }] = useMutation(CREATE_WALLET_MUTATION, {
    onCompleted: (data) => {
      setResult(data.createWallet);
    },
  });

  const handleCreateWallet = async () => {
    try {
      await createWallet({
        variables: {
          input: { network }
        }
      });
    } catch (err) {
      console.error('Create wallet error:', err);
    }
  };

  const copyToClipboard = (text) => {
    navigator.clipboard.writeText(text);
    alert('Copied to clipboard!');
  };

  const handleDone = () => {
    setResult(null);
    onSuccess();
  };

  if (result) {
    return (
      <div className="create-wallet-result">
        <div className="success-message">
          <div className="success-icon"></div>
          <h2>Wallet Created Successfully!</h2>
        </div>

        <div className="wallet-details">
          <div className="detail-group">
            <label>Wallet Address:</label>
            <div className="address-display-large">
              <span className="mono-text">{result.address}</span>
              <button
                onClick={() => copyToClipboard(result.address)}
                className="btn btn-small"
              >
                Copy
              </button>
            </div>
          </div>

          <div className="detail-group">
            <label>Network:</label>
            <span className="network-badge">{result.network}</span>
          </div>
        </div>

        <div className="mnemonic-warning">
          <h3> CRITICAL: Save Your Mnemonic Phrase!</h3>
          <p>
            This is your backup phrase. Write it down and store it securely offline. 
            It will NOT be shown again!
          </p>
          <div className="mnemonic-display">
            <div className="mnemonic-words">{result.mnemonic}</div>
            <button
              onClick={() => copyToClipboard(result.mnemonic)}
              className="btn btn-secondary"
            >
               Copy Mnemonic
            </button>
          </div>
          <div className="warning-footer">
            <strong> Never share this phrase with anyone!</strong>
            <p>Anyone with this phrase has full control of your wallet.</p>
          </div>
        </div>

        <button onClick={handleDone} className="btn btn-primary btn-large">
          Done, I've Saved My Mnemonic
        </button>
      </div>
    );
  }

  return (
    <div className="create-wallet-form">
      <h2> Create New Wallet</h2>
      <p className="subtitle">Create a new Ethereum wallet on the testnet</p>

      {error && (
        <div className="alert alert-error">
          Error creating wallet: {error.message}
        </div>
      )}

      <div className="form-group">
        <label>Network</label>
        <select
          value={network}
          onChange={(e) => setNetwork(e.target.value)}
          disabled={loading}
          className="select-input"
        >
          <option value="sepolia">Sepolia Testnet</option>
          <option value="goerli">Goerli Testnet</option>
        </select>
        <small>Testnets use fake ETH for testing - no real money involved</small>
      </div>

      <div className="info-box">
        <h4>What happens when I create a wallet?</h4>
        <ul>
          <li> A new Ethereum address will be generated</li>
          <li> Private keys are encrypted and stored securely</li>
          <li> You'll receive a mnemonic phrase for backup</li>
          <li> You can use the wallet immediately</li>
        </ul>
      </div>

      <button
        onClick={handleCreateWallet}
        disabled={loading}
        className="btn btn-primary btn-large"
      >
        {loading ? ' Creating Wallet...' : ' Create Wallet'}
      </button>
    </div>
  );
}

export default CreateWallet;
