import React, { useState, useEffect } from 'react';
import { useMutation, gql } from '@apollo/client';

const SEND_FUNDS_MUTATION = gql`
  mutation SendFunds($input: SendFundsInput!) {
    sendFunds(input: $input) {
      transaction {
        id
        transactionHash
        fromAddress
        toAddress
        amount
        status
      }
      hash
    }
  }
`;

function SendFunds({ wallets, selectedWallet, onSuccess }) {
  const [walletId, setWalletId] = useState('');
  const [toAddress, setToAddress] = useState('');
  const [amount, setAmount] = useState('');
  const [result, setResult] = useState(null);

  useEffect(() => {
    if (selectedWallet) {
      setWalletId(selectedWallet.id.toString());
    }
  }, [selectedWallet]);

  const [sendFunds, { loading, error }] = useMutation(SEND_FUNDS_MUTATION, {
    onCompleted: (data) => {
      setResult(data.sendFunds);
      setToAddress('');
      setAmount('');
      onSuccess();
    },
  });

  const handleSend = async (e) => {
    e.preventDefault();

    if (!walletId || !toAddress || !amount) {
      alert('Please fill in all fields');
      return;
    }

    if (!toAddress.startsWith('0x')) {
      alert('Please enter a valid Ethereum address');
      return;
    }

    if (parseFloat(amount) <= 0) {
      alert('Amount must be greater than 0');
      return;
    }

    try {
      await sendFunds({
        variables: {
          input: {
            walletId: parseInt(walletId),
            toAddress,
            amount
          }
        }
      });
    } catch (err) {
      console.error('Send funds error:', err);
    }
  };

  const copyToClipboard = (text) => {
    navigator.clipboard.writeText(text);
    alert('Copied to clipboard!');
  };

  if (result) {
    return (
      <div className="send-result">
        <div className="success-message">
          <div className="success-icon"></div>
          <h2>Transaction Sent!</h2>
        </div>

        <div className="transaction-details">
          <div className="detail-row">
            <label>Transaction Hash:</label>
            <div className="hash-display">
              <span className="mono-text">{result.hash.substring(0, 20)}...{result.hash.substring(result.hash.length - 10)}</span>
              <button
                onClick={() => copyToClipboard(result.hash)}
                className="btn btn-small"
              >
                Copy
              </button>
            </div>
          </div>

          <div className="detail-row">
            <label>From:</label>
            <span className="mono-text-small">{result.transaction.fromAddress}</span>
          </div>

          <div className="detail-row">
            <label>To:</label>
            <span className="mono-text-small">{result.transaction.toAddress}</span>
          </div>

          <div className="detail-row">
            <label>Amount:</label>
            <span className="amount-large">{result.transaction.amount} ETH</span>
          </div>

          <div className="detail-row">
            <label>Status:</label>
            <span className={`status-badge status-${result.transaction.status}`}>
              {result.transaction.status}
            </span>
          </div>
        </div>

        <div className="action-buttons">
          <a
            href={`https://sepolia.etherscan.io/tx/${result.hash}`}
            target="_blank"
            rel="noopener noreferrer"
            className="btn btn-secondary"
          >
             View on Etherscan
          </a>
          <button
            onClick={() => setResult(null)}
            className="btn btn-primary"
          >
            Send Another Transaction
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="send-funds-form">
      <h2> Send Funds</h2>
      <p className="subtitle">Send ETH from your wallet</p>

      {error && (
        <div className="alert alert-error">
          Error sending funds: {error.message}
        </div>
      )}

      <form onSubmit={handleSend}>
        <div className="form-group">
          <label>From Wallet</label>
          <select
            value={walletId}
            onChange={(e) => setWalletId(e.target.value)}
            disabled={loading}
            className="select-input"
          >
            <option value="">Select a wallet</option>
            {wallets.map((wallet, index) => (
              <option key={wallet.id} value={wallet.id}>
                Wallet #{index + 1} - {wallet.address.substring(0, 10)}...
              </option>
            ))}
          </select>
        </div>

        <div className="form-group">
          <label>To Address</label>
          <input
            type="text"
            value={toAddress}
            onChange={(e) => setToAddress(e.target.value)}
            placeholder="0x..."
            disabled={loading}
          />
          <small>Enter the recipient's Ethereum address</small>
        </div>

        <div className="form-group">
          <label>Amount (ETH)</label>
          <input
            type="text"
            value={amount}
            onChange={(e) => setAmount(e.target.value)}
            placeholder="0.001"
            disabled={loading}
          />
          <small>
            Recommended: Start with small amounts (0.001 - 0.01 ETH) on testnet
          </small>
        </div>

        <div className="info-box">
          <h4> Important Notes:</h4>
          <ul>
            <li>Transactions are irreversible once sent</li>
            <li>Gas fees will be deducted from your wallet</li>
            <li>This is testnet ETH (no real value)</li>
            <li>Transactions typically confirm in 15-30 seconds</li>
          </ul>
        </div>

        <button
          type="submit"
          disabled={loading || !walletId || !toAddress || !amount}
          className="btn btn-primary btn-large"
        >
          {loading ? ' Sending Transaction...' : ' Send Funds'}
        </button>
      </form>
    </div>
  );
}

export default SendFunds;
